import React, { Component } from 'react';
import MoSelector from './moSelector';
import Console from './console';
import UIcontrols from './uiControls';
import Timeline from "./timeline";
import TimelineConfig from "./timelineConfig";
import { Row, Button, Icon, Tooltip } from "antd";
import "antd/dist/antd.css";
import './hq.less';

// const hiGray = "rgba(255, 255, 255, 0.65)"

class Hq extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeUnit: 1,
      capture: false,
      dialPos: 0,
      activePointVal: null,
      areTimelinesPlaying: false,
      areTimelinesEditing: false,
      saveTrigger: false,
      clearTrigger: false,
      captureTrigger: 0,
      colors: this.getColors(this.props.allUnits, 1)
    };
  }

  getColors = (allUnits, activeUnit, activeOpacity = 0.9, inactiveOpacity = 0.2) => {
    let colors = allUnits.map((id, idx) => {
      const opacity = id === activeUnit ? activeOpacity : inactiveOpacity;
      return "hsla(" + ((idx) * 56 + 0) % 360 + ", 60%, 60%, " + opacity + ")";
    });
    return colors;
  }

  onPlayClick() {
    this.setState({ areTimelinesPlaying: !this.state.areTimelinesPlaying });
  }

  onEditClick() {
    this.setState({ areTimelinesEditing: !this.state.areTimelinesEditing });
  }

  onCaptureClick() {
    const { captureTrigger } = this.state;
    this.setState({ captureTrigger: captureTrigger + 1 })
  }

  componentDidUpdate(prevProps, prevState) {
    const { allUnits } = this.props;
    const { activeUnit } = this.state;
    if (allUnits !== prevProps.allUnits || activeUnit !== prevState.activeUnit) {
      const colors = this.getColors(allUnits, activeUnit);
      this.setState({ colors: colors });
    }
  }

  render() {
    const { activeUnit, colors } = this.state;
    const { allUnits, screen } = this.props;
    const playState = this.state.areTimelinesPlaying;
    let playIcon = playState === false ? "play-circle" : "pause-circle";
    const editState = this.state.areTimelinesEditing;
    let editIcon = editState === false ? "edit" : "lock";
    let topRow;
    let componentSize = "default";
    // let timelineTitle = "motion timeline";
    if (screen) {
      let console;
      if (screen.width < 600) {
        componentSize = "small";
        // timelineTitle = "";
      }
      else if (screen.width > 2000) {
        componentSize = "large";
      }
      if (screen.width > 800) {
        console = <div className="HQconsole">
          <Console />
        </div>
      }
      if (screen.height > 500) {
        topRow =
          <div className="HQtopContainer">
            {console}
            <div
              className="HQuiControls">
              <UIcontrols
                activeUnit={activeUnit}
                allUnits={allUnits}
                colors={colors}
                steps={this.props.steps}
                screen={this.props.screen}
                getCapture={(val) => this.setState({ capture: val })}
                getDialPos={(val) => this.setState({ dialPos: val })}
              />
            </div>
          </div>
      }
    }
    return (
      <div className="HQcontainer">
        {topRow}
        <Row type="flex" justify="start" align="top">
          <div className="HQtimelineBox">
            <div className="HQtimelineButtonRow">
              <Row type="flex" justify="space-between" align="middle">
                <div className="HQtimelineButtonBox">
                  <Tooltip title="Timeline Settings">
                    <Button type="link"
                      onClick={() => { this.TimelineConfig.showModal() }}
                      className="HQtimelineButtonUnit"
                    >
                      <Icon type="setting" className="HQbuttonText" />
                    </Button>
                  </Tooltip>
                  <Tooltip title="Edit/Lock">
                    <Button type="link"
                      onClick={this.onEditClick.bind(this)}
                      className="HQtimelineButtonUnit"
                    >
                      <Icon type={editIcon} className="HQbuttonText" />
                    </Button>
                  </Tooltip>
                  <Tooltip title="Play/Pause">
                    <Button type="link"
                      onClick={this.onPlayClick.bind(this)}
                      className="HQtimelineButtonUnit"
                    >
                      <Icon type={playIcon} className="HQbuttonText" />
                    </Button>
                  </Tooltip>
                  <Tooltip title="Capture Motor Positions">
                    <Button type="link"
                      onClick={this.onCaptureClick.bind(this)}
                      className="HQtimelineButtonUnit"
                    >
                      <Icon type="sync" className="HQbuttonText" />
                    </Button>
                  </Tooltip>
                  <TimelineConfig onRef={ref => (this.TimelineConfig = ref)}
                    saveTrigger={this.state.saveTrigger}
                    clearTrigger={this.state.clearTrigger}
                    getSaveTrigger={(val) => this.setState({ saveTrigger: val })}
                    getClearTrigger={(val) => this.setState({ clearTrigger: val })}
                  />
                </div>
                {/* {timelineTitle} */}
                <div className="HQmoSelector">
                    <MoSelector
                      size={componentSize}
                      defaultUnit={activeUnit}
                      allUnits={allUnits}
                      getColors={this.getColors}
                      getActiveUnit={(val) => this.setState({ activeUnit: val })}
                    />
                </div>
              </Row>
            </div>
            <Row type="flex" justify="start" align="bottom">
              <Timeline
                activeUnit={activeUnit}
                allUnits={allUnits}
                screen={this.props.screen}
                steps={this.props.steps}
                capture={this.state.capture}
                inPointVal={this.state.dialPos}
                makePlay={this.state.areTimelinesPlaying}
                makeEdit={this.state.areTimelinesEditing}
                makeClear={this.state.clearTrigger}
                makeSave={this.state.saveTrigger}
                makeCapture={this.state.captureTrigger}
                colors={colors}
                getActivePointVal={(val) => this.setState({ activePointVal: val })}
              />
            </Row>
          </div>
        </Row>
      </div>
    );
  };
};

export default Hq;
