import React, { Component } from 'react';
import './App.less';
import AppMenu from './components/menu';
import Hq from './components/hq';

class App extends Component {
  constructor() {
    super();
    this.state = {
      page: "hq",
      screen: {
        width: 0,
        height: 0,
      },
      steps: 4096,
    };
    this.updateDimensions = this.updateDimensions.bind(this);
  }

  componentDidMount() {
    window.addEventListener("resize", this.updateDimensions);
    setInterval(this.updateDimensions, 200);
    this.updateDimensions();
  }

  updateDimensions() {
    const { screen } = this.state;
    if (screen.width !== window.innerWidth || screen.height !== window.innerHeight) {
      this.setState({
        screen: {
          width: window.innerWidth,
          height: window.innerHeight
        }
      });
    }
    // console.log("width: " + window.innerWidth + ",  height: " + window.innerHeight);
  }

  render() {
    const { page, screen } = this.state;
    let content;
    if (page === "hq") {
      content =
        <Hq className="APhq" unitId={2} allUnits={[1, 2, 6, 11, 19, 25]} screen={screen} steps={this.state.steps} />
    }
    else if (page === "timelines") {
      content =
        <div className="APtimelineContainer">
        </div>
    }
    return (
      <div className="App">
        <div className="APmenu">
          <AppMenu getChoice={(choice) => this.setState({ page: choice })} />
        </div>
        <div className="APoffset"></div>
        {content}
      </div>
    );
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.updateDimensions);
  }
}

export default App;