import React from 'react'
import Terminal from 'react-console-emulator'


const globalStyle = {
    style: {
        background: "rgba(0, 0, 0, 1)",
        width: "100%",
        height: "100%",
        // height: "40vh",
        minHeight: "190px",
        minWidth: "250px"
    },
    contentStyle: {
        color: "rgba(255, 255, 255, 0.65)"
    },
    inputStyle: {
        color: "rgba(255, 255, 255, 0.65)",
        position: "relative",
        top: "3px"

    },
    promptLabelStyle: {
        color: "rgba(238, 156, 52, 0.75)"      
    }
}

export default class Console extends React.Component {
  constructor (props) {
    super(props)
    this.terminal = React.createRef()
  }

  commands = {
    echo: {
      description: 'Echo a passed string.',
      usage: 'echo <string>',
      fn: function () {
        return `${Array.from(arguments).join(' ')}`
      }
    },
    digitalWrite: {
      description: 'Write a high or low value to a pin.',
      usage: 'digitalWrite <pin> <value> <unitId>',
      fn: (pin, val, unitId) => {
        const terminal = this.terminal.current
        fetch('/digitalwrite/' + pin + '/' + val + '/' + unitId + '/')
        .then(res => res.json()).then(data => terminal.pushToStdout(JSON.stringify(data.reply)));  
        // return 'await reply';
      }
    },
    rotate: {
      description: 'Rotate at the given speed in the given direction.',
      usage: 'rotate <speed> <direction> <unitId>',
      fn: (speed, direction, unitId) => {
        const terminal = this.terminal.current
        fetch('/rotate/' + speed + '/' + direction + '/' + unitId + '/')
        .then(res => res.json()).then(data => terminal.pushToStdout(JSON.stringify(data.reply)));  
        // return 'await reply';
      }
    },
    wait: {
      description: 'Waits one second and sends a message.',
      usage: 'wait',      
      fn: () => {
        const terminal = this.terminal.current
        setTimeout(() => terminal.pushToStdout('Hello after 1 second!'), 1500)
        return 'Running, please wait...'
      }
    }
  }  
    
  render () {
    return (
      <Terminal
        ref={this.terminal} // Assign ref to the terminal here
        commands={this.commands}
        welcomeMessage={'console in development - type "help" for available commands'}
        promptLabel={'mo>'}
        {...globalStyle}
        />
    )
  }
}