import React from 'react';
import { Modal, Button } from 'antd';

class TimelineConfig extends React.Component {
    constructor(props) {
        super(props);
        this.state = { 
            visible: false,
        };
    }
        
    showModal = () => {
        this.setState({
            visible: true,
        });
    };

    componentDidMount() {
        this.props.onRef(this)
    }

    componentWillUnmount() {
        this.props.onRef(undefined)
    }

    handleOk = e => {
        console.log(e);
        this.setState({
            visible: false,
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            visible: false,
        });
    };

    render() {
        const {saveTrigger, clearTrigger} = this.props;
        // const saveTrigger = this.props.saveTrigger + 1;
        // const clearTrigger = this.props.clearTrigger + 1;
        return (
            <div>
                <Modal
                    title="Timeline Settings"
                    visible={this.state.visible}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                >
                    <Button type="primary"
                        size="small"
                        onClick={() => this.props.getSaveTrigger(saveTrigger + 1)}
                        // className="HQtimelineButtonUnit"
                    >
                        save
                        {/* <Icon type="setting" className="HQbuttonText" /> */}
                    </Button>
                    &nbsp;
                    <Button type="primary"
                        size="small"
                        onClick={() => this.props.getClearTrigger(clearTrigger + 1)}
                        // onClick={() => { }}
                        // className="HQtimelineButtonUnit"
                    >
                        clear
                        {/* <Icon type="setting" className="HQbuttonText" /> */}
                    </Button>
                </Modal>
            </div>
        );
    }
}

export default TimelineConfig;
