import React, { Component } from 'react';
import { Menu, Icon } from 'antd';
import "antd/dist/antd.css";
import './menu.less';

const { SubMenu } = Menu;

class AppMenu extends Component {
  constructor() {
    super();
    this.state = {
      current: 'hq',
    };
  }

  handleClick = e => {
    this.setState({
      current: e.key,
    });
    this.props.getChoice(e.key);
  };

  render() {
    return (
      <Menu onClick={this.handleClick} selectedKeys={[this.state.current]} mode="horizontal" theme="dark" className="MNmenu" >
        <SubMenu
          title={
            <span className="MNsubmenu-title-wrapper" style={{ color: "rgba(255, 255, 255, 0.6" }}>
              {/* <Icon type="setting" /> */}
              Mo Control
            </span>
          }
        >
          <Menu.ItemGroup className="MNmenuGroup">
            <Menu.Item key="hq" className="MNitem">
              <a href="index.html">
                Mo HQ
              </a>
            </Menu.Item>
            {/* <Menu.Item key="live">Live Motion</Menu.Item> */}
            <Menu.Item key="about"  className="MNitem">
              <a href="http://www.robotstack.com" rel="noopener noreferrer">
                About Mo
              </a>
            </Menu.Item>
          </Menu.ItemGroup>
        </SubMenu>
      </Menu>
    );
  }
}

export default AppMenu;
