import React, { Component } from 'react';
import classes from './analogReadButton.module.css';
import { Button } from "antd";

class AnalogReadButton extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: "click"
    };
  };

  onClick() {
    fetch('/analogread/' + this.props.pin + '/' + this.props.unitId + '/')
      .then(res => res.json()).then(data => { 
        console.log(data);
        if(data.reply){
          this.setState({ value: data.reply.params });
        }
      }).catch( () => 
        this.setState({ value: "null" })  
      );
  };

  render() {
    return (
      <div className={classes.container}>
        <Button 
          size={this.props.size} 
          onClick={this.onClick.bind(this)}
          className={classes.buttonUnit}
          >
          <span className={classes.budge}>{this.state.value}</span>
        </Button>
      </div>
    );
  };
};

export default AnalogReadButton;
