import React from 'react';
import { Pagination } from 'antd';

class MoSelector extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            activeUnit: this.props.allUnits[0],
            current: 1
        };
    };

    itemRender(current, type, originalElement) {
        const { allUnits, getColors } = this.props;
        const { activeUnit } = this.state;
        const idx = current - 1;
        const altLabel = "mo:" + allUnits[idx];
        const colors = getColors(allUnits, activeUnit, 1, 0.5);
        // const {activeUnit} = this.state;
        // const color = idx === allUnits.indexOf(activeUnit) ? colors[idx] : "rgba(255, 255, 255, 0.65)";
        if (type === 'page') {
            return <a alt={altLabel} style={{ color: colors[idx], fontSize: ".9em", padding: "0px" }}>{allUnits[idx]}</a>
        }
        return originalElement;
    }

    onChange = (idx) => {
        const { allUnits } = this.props;
        const activeUnit = allUnits[idx - 1];
        this.setState({
            activeUnit: activeUnit,
            current: idx,
        });
        this.props.getActiveUnit(activeUnit);
    };

    render() {
        const { allUnits } = this.props;
        const { current } = this.state;
        return <Pagination
            current={current}
            pageSize={1}
            onChange={this.onChange}
            total={allUnits.length}
            size={this.props.size}
            showLessItems={true}
            // showTitle="false"
            // hideOnSinglePage={true}
            style={{ position: "relative", bottom: "1px" }}
            itemRender={this.itemRender.bind(this)} />;
    }
}

export default MoSelector;
