import React, { Component } from 'react';
import classes from './analogWriteSlider.module.css';
import {Slider} from "antd";

class AnalogWriteSlider extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: 0
    };
  };

  componentDidMount() {
    fetch('/analogwrite/' + this.props.pin + '/' + this.state.value + '/' + this.props.unitId + '/')
      .then(res => res.json()).then(data => console.log(data));
  };

  onChange(val) {
    fetch('/analogwrite/' + this.props.pin + '/' + val + '/' + this.props.unitId + '/')
      .then(res => res.json()).then(data => console.log(data));  
    this.setState({
      value: val
    });
  };

  render() {
    return (
      <div className={ classes.container }>
        <Slider 
          vertical 
          max={255} 
          defaultValue={this.state.value}
          onChange={this.onChange.bind(this)} 
        />
      </div>
    );
  };
};

export default AnalogWriteSlider;
