import React, { Component } from 'react'
import Chart from "chart.js";
import '../dragData';           // custom dragData module  
import classes from "./timeline.module.css";
import "./pointColors.less";

let dragDisabled = true;
// const hiBlue = "rgb(61, 153, 255)";
// const loBlue = "rgba(61, 153, 255, 0.65)";
const hiGray = "rgba(255, 255, 255, 0.65)"
// const midGray = "rgba(255, 255, 255, 0.45)"
const loGray = "rgba(255, 255, 255, 0.2)"

export default class Timeline extends Component {
    constructor(props) {
        super(props);
        this.chartRef = React.createRef();
        this.state = {
            "numPoints": 10,
            "activePoint": 0,
            "activePointVal": null,
            "interval": 4000,
            "intervalId": null,
            "play": false,
            "edit": false,
        }
        this.saveDatasets.bind(this);
    }

    componentDidMount() {
        const { numPoints, interval } = this.state;
        const xLabels = Array.from({ length: numPoints }, (e, i) => (i * interval / 1000).toFixed(2));
        const chartlines = this.createChartLines();
        const isActiveDataset = (datasetIndex) => {
            const { allUnits, activeUnit } = this.props;
            let isActiveDataset = false;
            if (datasetIndex === allUnits.indexOf(activeUnit)) {
                isActiveDataset = true;
            }
            return isActiveDataset;
        }
        const refToSetActivePoint = (idx) => this.setActivePoint(idx);
        const refToSetActivePointValue = (val) => {
            this.setState({ activePointVal: val });
            this.props.getActivePointVal(val);
        }
        const chartConfig = {
            type: "line",
            data: {
                labels: xLabels,
                datasets: chartlines,
                backgroundColor: "rgba(0,0,0,0)",
            },
            options: {
                //Customize chart options
                dragData: true,
                dragX: false,
                dragDataRound: 0,
                activeLine: 0,
                onDragStart: function (e, element) {
                    const idx = element._index;
                    refToSetActivePoint(idx);
                    if (dragDisabled || !isActiveDataset(element._datasetIndex)) return false
                },
                onDrag: function (e, datasetIndex, index, value) {
                    if (isActiveDataset(datasetIndex)) {
                        refToSetActivePoint(index);
                        refToSetActivePointValue(value / 360);

                    }
                },
                onDragEnd: function (e, datasetIndex, index, value) {
                    if (isActiveDataset(datasetIndex)) {
                        refToSetActivePoint(index);
                        refToSetActivePointValue(value / 360);
                    }
                },
                scales: {
                    yAxes: [{
                        scaleLabel: {
                            display: true,
                            labelString: "degrees",
                            fontColor: hiGray,
                            padding: 0,
                        },
                        ticks: {
                            stepSize: 360,
                            suggestedMax: 360 * 3,
                            suggestedMin: -360 * 3,
                            padding: 0,
                            fontColor: hiGray,
                            fontSize: 11,
                        },
                        gridLines: {
                            // display: false,
                            color: loGray,
                        },
                    }],
                    xAxes: [{
                        scaleLabel: {
                            display: true,
                            labelString: "seconds",
                            fontColor: hiGray,
                        },
                        ticks: {
                            fontColor: hiGray,
                            fontSize: 11,
                        },
                        gridLines: {
                            // display: false,
                            color: loGray,
                        },
                    }],
                },
                animation: {
                    duration: this.state.interval / 2
                },
                legend: {
                    display: false,
                    labels: {
                        boxWidth: 0,
                    }
                },
                responsive: true,
                maintainAspectRatio: false,
                tooltips: {
                    mode: "nearest",
                    intersect: true,
                    // axis: "x"
                },
                hover: {
                    mode: "nearest",
                    intersect: true,
                    // axis: "x"
                },
                // onClick: (e, item) => console.log("onClick: " + item[0]._datasetIndex),
                layout: {
                    padding: {
                        top: 0,
                        left: 0,
                        right: 0,
                        bottom: 0
                    }
                },
            }
        }
        this.chartRef = this.chartRef.current.getContext("2d");
        this.chart = new Chart(this.chartRef, chartConfig);
        this.pause();
        this.lock();
        setInterval(this.saveDatasets, 10000);
    }

    setPointsRadii(numPoints, activePoint) {
        return Array.from({ length: numPoints }, (e, i) => i === activePoint ? 8 : 4);
    }

    createChartLines() {
        let datasets = JSON.parse(localStorage.getItem("storedDatasets"));
        if (!datasets) {
            datasets = this.clearDatasets();
        }
        return datasets
    }

    saveDatasets = () => {
        try {
            let datasetsToSave = [];
            this.chart.data.datasets.forEach((dataset, idx) => {
                datasetsToSave[idx] = { ...dataset };
                delete datasetsToSave[idx]["_meta"];
                datasetsToSave[idx].data = [...dataset.data];
            });
            localStorage.removeItem("storedDatasets");
            localStorage.setItem("storedDatasets", JSON.stringify(datasetsToSave));
        }
        catch {
            console.log("failed save");
        }
    }

    clearDatasets = () => {
        const { allUnits, colors } = this.props;
        const { numPoints } = this.state;
        const datasets = allUnits.map((id, idx) => {
            const data = new Array(numPoints).fill(idx * 90);
            const pointColors = new Array(numPoints).fill(colors[idx]);
            return {
                id: id,
                label: "MO" + id,
                data: data,
                pointBackgroundColor: pointColors,
                pointRadius: this.setPointsRadii(numPoints, 0),
                radius: 4,
                pointHitRadius: 25,
                pointHoverRadius: 8,
                pointHoverColor: "#FFF",
                borderColor: colors[idx],
                borderWidth: 1,
                fill: false,
            }
        });
        console.log("new datasets");
        this.saveDatasets();
        return datasets;
    }

    nextActivePoint() {
        const { allUnits, activeUnit } = this.props;
        const activeSetIndex = allUnits.indexOf(activeUnit);
        const numPoints = this.chart.data.datasets[activeSetIndex].data.length;
        let idx = (this.state.activePoint + 1) >= numPoints ? 0 : this.state.activePoint + 1;
        this.setActivePoint(idx);
    }

    setActivePoint(pointIndex) {
        this.setState({ activePoint: pointIndex });
        const { allUnits } = this.props;
        const { numPoints } = this.state;
        // const activeSetIndex = allUnits.indexOf(activeUnit);
        this.chart.data.datasets.forEach(ds => { ds.pointRadius = this.setPointsRadii(numPoints, pointIndex) });
        this.chart.update();
        allUnits.forEach((unitId, unitIndex) => {
            const destination = this.chart.data.datasets[unitIndex].data[pointIndex] / 360 * this.props.steps;
            this.moveIt(destination, unitId);
        });
        // this.moveIt(destination, activeUnit);
    }

    moveIt(destination, unitId) {
        if (this.state.play) {
            fetch('/rotateTo/' + destination + '/' + 255 + '/' + unitId + '/')
                .then(res => res.json()).then(data => {
                    console.log(data);
                });
        }
    }

    getMotiPos = (unitId) => (fetch('/get/R_PRESENT_POSITION/1/' + unitId + '/')
        .then(res => res.json())
        .then(data => {
            let motiPos;
            if (data.reply.params) {
                motiPos = data.reply.params[0] / this.props.steps;
            }
            return motiPos;
        })).catch(err => console.log(err))

    idle = (unitId) => (fetch('/rotate/0/0/' + unitId + '/')
        .then(res => res.json())
        .then(data => {
            console.log(data);
        })).catch(err => console.log(err))

    capture() {
        const { allUnits } = this.props;
        const { activePoint } = this.state;
        // const activeSetIndex = allUnits.indexOf(activeUnit);
        allUnits.forEach(async (unitId, unitIndex) => {
            await this.idle(unitId);
        });
        allUnits.forEach(async (unitId, unitIndex) => {
            const pos = await this.getMotiPos(unitId)
            console.log(pos * 360);
            if (pos !== null && isNaN(pos) === false) {
                this.chart.data.datasets[unitIndex].data[activePoint] = pos * 360;
            }
            this.chart.update();
        });
    }

    edit() {
        // console.log("edit");
        this.pause();
        this.setState({ edit: true });
        this.setActivePoint(0);
        dragDisabled = false;
    }

    lock() {
        // console.log("lock");
        this.setState({ edit: false });
        dragDisabled = true;
    }

    play() {
        // console.log("play");
        this.lock();
        this.setState({ play: true });
        const intervalId = setInterval(() => this.nextActivePoint(), this.state.interval);
        this.setState({ intervalId: intervalId });
    }

    pause() {
        // console.log("pause");
        this.setState({ play: false });
        const intervalId = this.state.intervalId;
        if (intervalId) {
            clearInterval(intervalId);
        }
        this.setState({ intervalId: null });
    }

    componentDidUpdate(prevProps, prevState) {
        const { makePlay, makeEdit, makeSave, makeClear, makeCapture, allUnits, activeUnit, inPointVal, colors } = this.props;
        const activeSetIndex = allUnits.indexOf(activeUnit);
        if (makePlay !== prevProps.makePlay) {
            makePlay ? this.play() : this.pause();
        }
        if (makeEdit !== prevProps.makeEdit) {
            makeEdit ? this.edit() : this.lock();
        }
        if (makeSave !== prevProps.makeSave) {
            this.saveDatasets();
        }
        if (makeClear !== prevProps.makeClear) {
            this.chart.data.datasets = this.clearDatasets();
            this.chart.update();
            this.saveDatasets();
        }
        if (makeCapture !== prevProps.makeCapture) {
            this.capture();
        }
        if (inPointVal != null && inPointVal !== prevProps.inPointVal) {
            if (makeEdit) {
                const { activePoint } = this.state;
                this.chart.data.datasets[activeSetIndex].data[activePoint] = inPointVal * 360;
                this.chart.update();
                this.setState({ activePointVal: inPointVal });
            }
        }
        if (activeUnit !== prevProps.activeUnit) {
            const idx = allUnits.indexOf(activeUnit);
            this.chart.options.activeLine = idx;
            // console.log("new active: " + idx);
        }
        if (colors !== prevProps.colors) {
            const { numPoints } = this.state;
            this.chart.data.datasets.forEach((dataset, idx) => {
                const pointColors = new Array(numPoints).fill(colors[idx]);
                dataset.pointBackgroundColor = pointColors;
                dataset.borderColor = colors[idx];
            });
            this.chart.update();
        }
    }

    render() {
        return (
            <div className={classes.graphContainer}>
                <canvas
                    // height="200px"
                    id="chart"
                    ref={this.chartRef}
                    className={classes.canvas}
                />
            </div>
        )
    }
}