import React, { Component } from 'react';
import classes from "./dial.module.css";
// import socketIOClient from "socket.io-client";
import { CircularInput, CircularTrack, CircularProgress, CircularThumb } from 'react-circular-input'
// import { Button, Icon } from 'antd';
// const hiBlue = "rgb(61, 153, 255)";
// const midGray = "rgba(255, 255, 255, 0.45)"
const hiGray = "rgba(255, 255, 255, 0.65)"
const loGray = "rgba(255, 255, 255, 0.2)"

class Dial extends Component {
  constructor(props) {
    super(props);
    this.state = {
      rotations: 0,
      dialPos: 0,
      lastDialVal: 0,
      motiPos: 0,
    };
  };

  componentDidMount() {
    this.getMotiPos();
    setInterval(this.getMotiPos, 250);
  };

  getMotiPos = () => (fetch('/get/R_PRESENT_POSITION/1/' + this.props.unitId + '/')
    .then(res => res.json())
    .then(data => {
      if (data.reply.params) {
        const motiPos = data.reply.params[0] / this.props.steps;
        if (motiPos !== null && isNaN(motiPos) === false) {
          this.setState({ motiPos: motiPos });
        }
      }
    })).catch(err => {console.log(err)})

  getRangeValue(val) {
    const rangeValue = Math.round(val * 360);
    return rangeValue;
  }

  checkRollover(val) {
    let rollover = 0;
    const thresh = .8;
    const lastDialVal = this.state.lastDialVal;
    if (val < (lastDialVal - thresh)) {
      rollover = 1;
    }
    else if (val > (lastDialVal + thresh)) {
      rollover = -1;
    }
    return rollover;
  }

  moveIt() {
    let destination = this.state.dialPos * this.props.steps;
    fetch('/rotateTo/' + destination + '/' + this.props.speed + '/' + this.props.unitId + '/')
      .then(res => res.json()).then(data => {
        console.log(data);
      });
  }

  changeValue(val) {
    if (this.props.capture) {
      // ignore user input if capturing with motor 
    }
    else {
      const rotations = this.state.rotations + this.checkRollover(val);
      const dialPos = (val + rotations);
      this.setState({
        lastDialVal: val,
        rotations: rotations,
        dialPos: dialPos,
      });
      this.props.getDialPos(dialPos);
      this.moveIt();
    }
  };

  componentDidUpdate(prevProps, prevState) {
    const { incomingVal, capture, getDialPos, color } = this.props;
    const { motiPos, dialPos } = this.state;
    if (incomingVal !== null && incomingVal !== prevProps.incomingVal) {
      this.setState({
        dialPos: incomingVal,
        lastIncomingVal: incomingVal
      });
      if (capture === false) {
        // ignore dial input if capturing from motor 
        this.moveIt();
      }
    }
    if (capture && motiPos !== prevState.motiPos) {
      this.setState({
        dialPos: motiPos,
        lastDialVal: motiPos,
      })
    }
    if (dialPos !== prevState.dialPos) {
      getDialPos((motiPos));
    }
    console.log(color);
  }

  render() {
    const { color } = this.props;
    const { motiPos, dialPos } = this.state;
    // const dialPos = capture ? motiPos : this.state.dialPos;
    let dialPos360 = this.getRangeValue(dialPos);
    let motiPos360 = this.getRangeValue(motiPos);
    const { radius, capture } = this.props;
    const topText = capture ? "(" + motiPos360 + ")" : dialPos360 + " ";
    const subText = capture ? "" : "(" + motiPos360 + ")";
    return (
      <div className={classes.container}>
        <CircularInput onChange={this.changeValue.bind(this)} value={dialPos} radius={radius}>
          <CircularTrack strokeWidth={1} stroke={color} />
          <CircularProgress strokeWidth={0} stroke={loGray} />
          <CircularThumb strokeWidth={1} stroke={color} fill={color} r={7} />
          {/* range value in center */}
          <text x={radius} y={radius} textAnchor="middle" dy="0.3em" fill={hiGray}>
            {topText}
          </text>
          <text x={radius} y={radius + 16} textAnchor="middle" fontSize="10px" fill={loGray}>
            {subText}
          </text>
        </CircularInput>
        <br />
      </div>
    )
  }
};

export default Dial;
